/* petrona-100 - latin */
@font-face {
  font-family: 'Petrona';
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: url('./petrona-v28-latin-100.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('./petrona-v28-latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('./petrona-v28-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('./petrona-v28-latin-100.woff') format('woff'), /* Modern Browsers */
       url('./petrona-v28-latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
       url('./petrona-v28-latin-100.svg#Petrona') format('svg'); /* Legacy iOS */
}

